import * as React from "react";
import { PropsWithChildren } from "react";
import { useTranslation } from "react-i18next";

type Props = PropsWithChildren<{
  sidebar: boolean;
  toggle(): void;
}>;

const Hamburger: React.FC<Props> = (props: Props) => {
  const { toggle, sidebar } = props;

  const handleKeyDown = (event: React.KeyboardEvent<HTMLDivElement>) => {
    if (event.key === "Enter") {
      toggle();
    }
  };

  const { t } = useTranslation("global");
  return (
    <div
      role="button"
      className={sidebar ? "m-hamburger" : "m-hamburger"}
      onClick={() => toggle()}
      onKeyDown={handleKeyDown}
      tabIndex={0}
      aria-label={t("Hamburger.label")}
    >
      <div className={`a-bar ${sidebar ? "a-bar--sidebar a-bar--top" : ""}`} />
      <div className={`a-bar ${sidebar ? "a-bar--sidebar a-bar--mid" : ""}`} />
      <div className={`a-bar ${sidebar ? "a-bar--sidebar a-bar--bottom" : ""}`} />
    </div>
  );
};

export default Hamburger;
