import React, { PropsWithChildren, useEffect, useRef } from "react";

import Hamburger from "./hamburger/Hamburger";
import Navbar from "./navbar/Navbar";
import { ILink } from "./navbarLinks/NavbarLinks";
import Sidebar from "./sidebar/Sidebar";
import { useSidebar } from "./sidebar/useSidebar";

type PropsHeader = PropsWithChildren<{
  navbarLinksList: ILink[];
  useLinks: ILink[];
}>;
export const Header: React.FC<PropsHeader> = ({ navbarLinksList, useLinks }: PropsHeader) => {
  const [sidebar, toggle] = useSidebar();
  const mHeader = useRef() as React.MutableRefObject<HTMLDivElement>;

  function setHeight() {
    if (!mHeader.current) return;
    const { height } = mHeader.current.getBoundingClientRect();
    document.documentElement.style.setProperty("--vh", `${window.innerHeight * 0.01}px`);
    document.documentElement.style.setProperty("--header-height", `${height}px`);
  }
  function setMargins() {
    const maxBoxed = getComputedStyle(document.documentElement).getPropertyValue("--max-boxed");
    const maxBoxedSmall = getComputedStyle(document.documentElement).getPropertyValue("--max-boxed-small");
    const widthScreen = window.innerWidth;
    const marginBoxed = (widthScreen - Number(maxBoxed.split("px")[0])) / 2;
    const marginBoxedSmall = (widthScreen - Number(maxBoxedSmall.split("px")[0])) / 2;
    document.documentElement.style.setProperty("--margin-boxed-container", `${marginBoxed}px`);
    document.documentElement.style.setProperty("--max-boxed-small", `${marginBoxedSmall}px`);
  }
  function onResize() {
    setHeight();
    setMargins();
  }
  useEffect(() => {
    setHeight();
    setMargins();
    window.addEventListener("resize", onResize);
    return () => window.removeEventListener("resize", onResize);
  }, []);

  return (
    <div ref={mHeader} className={"m-header"}>
      <div className={`m-header__overlay ${sidebar ? "m-header__overlay--sidebar" : ""}`} onClick={toggle} />
      <div className="c-header">
        <Navbar navbarLinksList={navbarLinksList} useLinks={useLinks} />
        <Hamburger sidebar={sidebar} toggle={toggle} />
      </div>
      <Sidebar navbarLinksList={navbarLinksList} useLinks={useLinks} sidebar={sidebar} toggle={toggle} />
    </div>
  );
};

export default Header;
