import * as React from "react";

type PropsDoNotMountComponent = { sms: string };

const DoNotMountComponent: React.FC<PropsDoNotMountComponent> = (props: PropsDoNotMountComponent) => {
  if (process.env.GATSBY_ENV !== "development") return <></>;

  return (
    <section className={"o-do-not-mount-component"}>
      <div className={"c-do-not-mount-component"}>
        <p>{props.sms}</p>
      </div>
    </section>
  );
};

export default DoNotMountComponent;
