import { Link } from "gatsby";
import { StaticImage } from "gatsby-plugin-image";
import React from "react";
import { useTranslation } from "react-i18next";

import { getSlugWithLang } from "../../../shared/utils/getSlugWithLang";
import NavbarLinks, { ILink } from "../navbarLinks/NavbarLinks";

type PropsNavbar = {
  navbarLinksList: ILink[];
  useLinks: ILink[];
};
const Navbar: React.FC<PropsNavbar> = ({ navbarLinksList, useLinks }: PropsNavbar) => {
  const { t, i18n } = useTranslation("global");
  return (
    <div className="m-navbar">
      <Link
        className={"navbar__logo"}
        to={`${getSlugWithLang("/", i18n.language)}`}
        title={`${t("Navbar.LinkTitle")}`}
        aria-label={`${t("Navbar.LinkLabel")}`}
      >
        <StaticImage
          src={"../../../../assets/icons/logo.svg"}
          alt={t("Navbar.StaticImageAlt")}
          width={215}
          height={30}
        />
      </Link>
      <div className={"m-navbar__links"}>
        <NavbarLinks infoLinks={navbarLinksList} useLinks={useLinks} desktop />
      </div>
    </div>
  );
};

export default Navbar;
