import "../../styles/index.scss";

import i18next from "i18next";
import * as React from "react";
import { useState } from "react";
import { I18nextProvider } from "react-i18next";

import global_en from "../../../content/shared/en/global.json";
import global_es from "../../../content/shared/es_es/global.json";
import { defaultLanguage, Language } from "../../../nubarchiva/settings/translations";
import Toolbar from "../Toolbar/Toolbar";
import { Footer } from "./footer/footer";
import Header from "./header/Header";
import { ILink } from "./header/navbarLinks/NavbarLinks";

interface Props {
  children: JSX.Element[] | JSX.Element;
  lang?: Language;
  landingName?: string;
  navbarLinksList: ILink[];
  useLinks: ILink[];
  legalLinks: ILink[];
}

const Layout: React.FC<Props> = (props: Props): JSX.Element => {
  const [lang, setLang] = useState<Language>(props.lang || defaultLanguage);
  i18next.init({
    interpolation: { escapeValue: false },
    lng: lang,
    resources: {
      es_es: {
        global: global_es,
      },
      en: {
        global: global_en,
      },
    },
  });
  // i18next.changeLanguage(lang);

  return (
    <I18nextProvider i18n={i18next}>
      <div className={`l-global l-${props.landingName}`}>
        <Toolbar />
        <Header navbarLinksList={props.navbarLinksList} useLinks={props.useLinks} />
        {props.children}
        <Footer legalLinks={props.legalLinks} />
      </div>
    </I18nextProvider>
  );
};

export default Layout;
