import { useState } from "react";

export const useSidebar = (): [boolean, () => void] => {
  const [sidebar, setSidebar] = useState(false);

  const toggleSidebar = () => {
    if (sidebar) {
      setSidebar(false);
    } else {
      setSidebar(true);
    }
  };
  return [sidebar, toggleSidebar];
};
