import * as React from "react";
import { PropsWithChildren } from "react";
import { useTranslation } from "react-i18next";

import { check_svg, fail_svg } from "../../Forms/components/elements_form";
import { close_svg } from "../../Modals/elements_modal";
import { ID_SETTING_COOKIES, StatusCookie } from "../BannerCookies";

type PropsBannerCookiesSettings = PropsWithChildren<{
  changeToTrue: () => void;
  changeToFalse: () => void;
  setViewSetting: (status: boolean) => void;
  cookieTag: StatusCookie;
}>;

const BannerCookiesSettings: React.FC<PropsBannerCookiesSettings> = (props: PropsBannerCookiesSettings) => {
  const { t } = useTranslation("global");
  const editorCookies = t("BannerCookiesSettings.editorCookies", { returnObjects: true });
  const thirdPartyCookies = t("BannerCookiesSettings.thirdPartyCookies", { returnObjects: true });
  return (
    <div id={ID_SETTING_COOKIES} className={"o-banner-settings-cookies"}>
      <button
        className={"m-banner-cookies__close"}
        onClick={() => {
          props.setViewSetting(false);
        }}
        aria-label={`${t("BannerCookiesSettings.labelClose")}`}
      >
        {close_svg}
        <span>{t("BannerCookiesSettings.close")}</span>
      </button>
      <div className="c-banner-settings-cookies">
        <div>
          <div className={"m-banner-cookies__info"}>
            <p className={"a-banner-cookies__title"}>{t("BannerCookiesSettings.configTitle")}</p>
          </div>
        </div>
      </div>
      <div className="c-banner-cookies__table">
        <table className={"m-banner-cookies__table"}>
          <thead>
            <tr>
              <th colSpan={3}>{t("BannerCookiesSettings.editorCookiesTitle")}</th>
              <th align={"center"} colSpan={2}>
                <b className={`${props.cookieTag === StatusCookie.ACCEPT ? "is-success" : "is-danger"}`}>
                  {props.cookieTag === StatusCookie.ACCEPT
                    ? t("BannerCookiesSettings.accepted")
                    : t("BannerCookiesSettings.declined")}
                </b>
              </th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <th>{t("BannerCookiesSettings.property")}</th>
              <th>{t("BannerCookiesSettings.cookie")}</th>
              <th>{t("BannerCookiesSettings.purpose")}</th>
              <th align={"center"}>{t("BannerCookiesSettings.term")}</th>
              <th align={"center"}>{t("BannerCookiesSettings.status")}</th>
            </tr>
            {editorCookies.map(
              (editorCookie: { property: string; cookie: string; purpose: string; term: string }, index: number) => (
                <tr key={index}>
                  <td>{editorCookie.property}</td>
                  <td>{editorCookie.cookie}</td>
                  <td>{editorCookie.purpose}</td>
                  <td align={"center"}>{editorCookie.term}</td>
                  <td
                    className={`m-banner-cookies__status ${
                      props.cookieTag === StatusCookie.ACCEPT ? "is-success" : "is-danger"
                    }`}
                  >
                    {props.cookieTag === StatusCookie.ACCEPT ? check_svg : fail_svg}
                  </td>
                </tr>
              )
            )}
            <tr>
              <th colSpan={6}>{t("BannerCookiesSettings.thirdPartyCookiesTitle")}</th>
            </tr>
            <tr>
              <th align={"center"} colSpan={2}>
                {t("BannerCookiesSettings.editor")}
              </th>
              <th colSpan={3} align={"center"}>
                {t("BannerCookiesSettings.editorPrivacyPolicy")}
              </th>
            </tr>
            {thirdPartyCookies.map(
              (thirdPartyCookie: { editor: string; editorPrivacyPolicy: string }, index: number) => (
                <tr key={index}>
                  <td colSpan={2}>{thirdPartyCookie.editor}</td>
                  <td colSpan={3}>
                    <a
                      href={thirdPartyCookie.editorPrivacyPolicy}
                      target={"_blank"}
                      rel={"noopener nofollow noreferrer"}
                    >
                      {thirdPartyCookie.editorPrivacyPolicy}
                    </a>
                  </td>
                </tr>
              )
            )}
          </tbody>
        </table>
      </div>
      <div className="c-banner-settings-cookies">
        <div className="m-banner-cookies__buttons">
          <button className={"a-link"} onClick={props.changeToFalse}>
            {t("BannerCookies.decline")}
          </button>
          <button className={"a-button"} onClick={props.changeToTrue}>
            {t("BannerCookiesSettings.acceptAllCookies")}
          </button>
        </div>
        <p className={"m-banner-cookies__footer"}>
          {t("BannerCookiesSettings.info")} {""}
          <a
            className={"footer__link"}
            title={"Ver poltica de cookies"}
            href="/legal/politica-de-cookies/"
            target="_blank"
          >
            {t("BannerCookiesSettings.here")}
          </a>
        </p>
      </div>
    </div>
  );
};

export default BannerCookiesSettings;
