import * as React from "react";
import { PropsWithChildren } from "react";

import LanguageSelector from "../LanguageSelector/LanguageSelector";
import NavbarLinks, { ILink } from "../navbarLinks/NavbarLinks";

type Props = PropsWithChildren<{
  sidebar: boolean;
  navbarLinksList: ILink[];
  useLinks: ILink[];
  toggle: () => void;
}>;

const Sidebar: React.FC<Props> = (props: Props) => {
  return (
    <div className={`m-sidebar ${props.sidebar ? "m-sidebar--active" : ""}`}>
      <LanguageSelector />
      <NavbarLinks useLinks={props.useLinks} infoLinks={props.navbarLinksList} />
    </div>
  );
};

export default Sidebar;
