import { defaultLanguage, Language } from "../../../../nubarchiva/settings/translations";
import { cleanSlashInPath } from "./cleanSplash";
import { getCodeLang } from "./getCodeLang";

export function getSlugWithLang(slug: string, lang: string): string {
  if (lang === defaultLanguage) {
    if (slug === "/") {
      return "/";
    }
    return cleanSlashInPath(`/${slug}/`);
  }
  if (slug === "/") {
    return `/${lang}/`;
  }
  return cleanSlashInPath(`/${lang}/${slug}/`);
}

export function getLangByRelativeDirectory(relativeDirectory: string): string {
  switch (true) {
    case relativeDirectory.includes(Language.ES_ES) || relativeDirectory.includes(getCodeLang(Language.ES_ES)):
      return Language.ES_ES;
    case relativeDirectory.includes(Language.EN_EN):
      return Language.EN_EN;
    default: {
      return defaultLanguage;
    }
  }
}
