import { Link } from "gatsby";
import * as React from "react";
import { PropsWithChildren } from "react";
import { useTranslation } from "react-i18next";

import DoNotMountComponent from "../../../DoNotMountComponent/DoNotMountComponent";
import { getSlugWithLang } from "../../../shared/utils/getSlugWithLang";
import { corner_svg } from "./navbarlinks_elements";

export type ILink = {
  slug: string | null;
  name: string | null;
};

type PropslinksList = PropsWithChildren<{
  infoLinks: ILink[];
  useLinks: ILink[];
  desktop?: boolean;
}>;

const NavbarsLinks: React.FC<PropslinksList> = (props: PropslinksList) => {
  // const usesLinks: ILink[] = props.useLinks; TODO descomentar para activar uses
  const usesLinks: ILink[] = [];
  const [isDropdownOpen, setIsDropdownOpen] = React.useState(false);
  const toggleDropdown = () => {
    setIsDropdownOpen(!isDropdownOpen);
  };
  if (!props.infoLinks) return <DoNotMountComponent sms={"Faltan items del menu"} />;
  const { t, i18n } = useTranslation("global");
  const button = props.infoLinks[props.infoLinks.length - 1];
  const linksWithoutButton = [...props.infoLinks];
  linksWithoutButton.pop();

  // todo si no es el lenguaje por defecto agregar el prefijo que toque

  const uses_render = usesLinks?.map((link: ILink, index: number) => (
    <Link
      key={index}
      activeClassName={"navbarlinks__item--active"}
      className={"navbarlinks__item navbarlinks__item--dropdown"}
      to={`/${link.slug === "/" ? "" : `${link.slug}/`}`}
    >
      {link.name}
    </Link>
  ));

  return (
    <div className={`m-navbarlinks ${props.desktop ? "m-navbarlinks--desktop" : "m-navbarlinks--sidebar"}`}>
      <div
        className={`navbarlinks__menu ${props.desktop ? "navbarlinks__menu--desktop" : "navbarlinks__menu--sidebar"}`}
      >
        {linksWithoutButton.map((link: ILink, index: number) => (
          <Link
            key={index}
            activeClassName={"navbarlinks__item--active"}
            className={"navbarlinks__item"}
            title={`${t("goto")} ${link.name}`}
            to={`${getSlugWithLang(`${link.slug}`, i18n.language)}`}
          >
            <span>{corner_svg}</span>
            {link.name}
          </Link>
        ))}

        {usesLinks?.length > 1 && (
          <div className={`navbarlinks__dropdown-button`}>
            <button className={"navbarlinks__item"} onClick={toggleDropdown}>
              <span>{corner_svg}</span>
              {t("uses.button")}
            </button>
            <div className={`navbarlinks__dropdown navbarlinks__dropdown--${isDropdownOpen ? "open" : "close"}`}>
              {uses_render}
            </div>
          </div>
        )}
      </div>
      <div className={"navbarlinks__cta"}>
        <Link
          activeClassName={"navbarlinks__item--active"}
          className={"a-button"}
          to={getSlugWithLang(`${button.slug}`, i18n.language)}
        >
          {button.name}
        </Link>
      </div>
    </div>
  );
};

export default NavbarsLinks;
