import { defaultLanguage } from "../../../../nubarchiva/settings/translations";

export function getCodeLang(language: string | null | undefined): string {
  const code = language || defaultLanguage;

  return code.replace("_", "-");
}

export function getLangNormalize(language: string | null | undefined): string {
  const code = language || defaultLanguage;
  return code.replace("-", "_");
}
